.elder-login {
  font-family: "Roboto";
  height: 100vh;
  width: 100%;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.app-name {
  animation-duration: 2s;
  color: rgb(73, 73, 73);
  font-size: 20vw;
}

.app-slogan {
  animation-duration: 2s;
  color: rgba(68, 67, 67, 0.8);
  font-size: 4vw;
}

.elder-form {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 35vh;
  width: 75vw;
  background-color: rgba(255, 254, 254, 0.8);
  border-radius: 10px;
  padding: 5vw;
  box-shadow: 0px 3px 11px 3px rgba(132, 131, 131, 0.8);
  color: rgba(68, 67, 67, 0.8);
  margin-top: 7.5vh;
}

.elder-form input {
  border: none;
  outline: none;
  background-color: transparent;
  border-bottom: 1px solid grey;
  font-size: 5vw;
  margin-top: 1vw;
  width: 70%;
}

.elder-form button {
  color: rgba(68, 67, 67, 0.8);
  font-size: 4vw;
  width: 50%;
  padding: 1vh;
  margin-top: 3vh;
  cursor: pointer;
}

.elder-form p {
  cursor: pointer;
  align-self: flex-end;
}

.swal2-title,
.swal2-confirm {
  font-family: "Roboto";
}

@media (min-width: 700px) {
  .app-name {
    font-size: 8vw;
  }

  .app-slogan {
    font-size: 1.5vw;
  }

  .elder-form {
    height: 40vh;
    width: 40vw;
    padding: 4vw;
  }

  .elder-form h1 {
    font-size: 3vw;
  }

  .elder-form input {
    font-size: 1.5vw;
    margin-top: 1vw;
    width: 70%;
  }

  .elder-form button {
    font-size: 1.5vw;
    width: 30%;
  }

  .elder-form p {
    font-size: 1.5vw;
    cursor: pointer;
    align-self: flex-end;
  }

  .swal2-title,
  .swal2-confirm {
    font-family: "Roboto";
  }
}
